import * as React from 'react'
import * as lodash from 'lodash'

const debounce = lodash.debounce
interface IProps {
  w: number
  h: number
}
interface ISrops {
  init: boolean
}
export default class AdaptWrapper extends React.PureComponent<IProps, ISrops> {
  static defaultProps = {
    w: 1920,
    h: 1080,
  }
  private wrpEl: HTMLDivElement
  private style: React.CSSProperties

  constructor(props: IProps) {
    super(props)
    const { w, h } = this.props
    this.style = {
      position: 'absolute',
      width: w + 'px',
      height: h + 'px',
      transformOrigin: '0 0',
      overflow: 'hidden',
      backgroundSize: '100% 100%',
    }
    this.state = {
      init: false,
    }
  }

  componentDidMount() {
    this.resizeWindow()
    this.setState({
      init: true,
    })
    window.addEventListener('resize', debounce(this.resizeWindow))
  }

  componentWillUnmount() {
    window.removeEventListener('resize', debounce(this.resizeWindow))
  }

  resizeWindow = () => {
    const { w, h } = this.props
    const htmlw = document.documentElement!.clientWidth

    // const  htmlh = document.documentElement!.clientHeight
    if (htmlw !== w) {
      const x = htmlw / w
      // const  y = htmlh / h
      //   global.screen.xScale = x;
      //   global.screen.yScale = y;

      this.wrpEl.style.width = w + 'px'
      this.wrpEl.style.height = h + 'px'
      this.wrpEl.style.transform = `scale(${x})`
      this.wrpEl.style.top = '0px'
      this.wrpEl.style.left = '0px'
      //   this.mapWrapperEl.style.width = w * x + 'px';
      //   this.mapWrapperEl.style.height = h * y + 'px';
      //   this.mapWrapperEl.style.transform = `scale(${1 / x},${1 / y + 0.0001})`;
    } else {
      //   global.screen.xScale = 1;
      //   global.screen.yScale = 1;
      this.wrpEl.style.width = '100%'
      this.wrpEl.style.height = '100%'
      this.wrpEl.style.transform = ''
      //   this.mapWrapperEl.style.width = '100%';
      //   this.mapWrapperEl.style.height = '100%';
      //   this.mapWrapperEl.style.transform = '';
    }
  }

  saveEl = (el: HTMLDivElement) => {
    this.wrpEl = el
  }

  render() {
    return (
      <div ref={this.saveEl} style={this.style}>
        {this.state.init && this.props.children}
      </div>
    )
  }
}



